@import '../config/_breakpoints';
@import './variables';
@import '../config/_animations';

.about {
  min-height: 100vh;
  width: 100vw;
  position: relative;
  z-index: -1;
  display: flex;
  align-items: center;
  justify-content: center;

  @include media($breakpoint-md) {
    padding: 64px; }

  @include media($breakpoint-sm) {
    padding: 16px;
    margin-top: 64px; } }

.about__text {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.about__header {
  font-size: 60px;
  font-weight: 100;
  margin: 0;

  @include media($breakpoint-sm) {
    font-size: 46px; } }

.about__hr {
  width: 500px;
  max-width: 90vw; }

.about__paragraph {
  font-size: 18px;
  font-weight: 300;
  width: 500px;
  text-align: center;
  max-width: 90vw;

  @include media($breakpoint-sm) {
    font-size: 16px; } }
