@import '../config/_animations';

.nav {
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 36px 36px 0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 4;
  opacity: 0;
  animation: 1s fadeIn forwards; }

.nav__icon {
  width: 36px; }

.nav__menu {
  width: 32px;
  height: 32px;
  position: relative;
  cursor: pointer; }

.nav__bar {
  height: 1px;
  background: #000000;
  position: absolute;
  transition: 0.5s transform, 0.5s background; }

.nav__bar--1 {
  top: 0;
  right: 0;
  width: 50%;
  transform-origin: top right; }

.nav__bar--2 {
  top: 50%;
  width: 100%; }

.nav__bar--3 {
  top: 100%;
  bottom: 0;
  left: 0;
  width: 50%;
  transform-origin: bottom left; }

.nav__menu.active {
  .nav__bar {
    background: white; }

  .nav__bar--1 {
    transform: rotate(-45deg) scaleX(1.4); }

  .nav__bar--2 {
    transform: rotate(45deg) scaleX(1.4); }

  .nav__bar--3 {
    transform: rotate(-45deg) scaleX(1.4); } }

.nav__menu:hover:not(.active) {
  .nav__bar--1 {
    transform: translateX(8px); }
  .nav__bar--3 {
    transform: translateX(-8px); } }
